.bottomLeft {
  display: flex;
  gap: 15px;
  padding: 10px;
  cursor: pointer;
}

.bottomLeft:hover {
  background-color: #688866;
}

.bottomLeft .contactImg {
  border-radius: 50%;
  width: 50px;
  height: 50px;
  object-fit: cover;
}

.bottomLeft .contactInfo {
  display: flex;
  flex-direction: column;
  flex: 1;
  margin-top: 5px;
}

.bottomLeft .contactName {
  margin: 0;
  font-size: 15px;
}

.bottomLeft .lastMsg {
  font-size: 10px;
  margin-top: -5px;
  color: lightgray;
}

.bottomLeft .more {
  margin-top: 10px;
}

.bottomLeft .more:hover {
  color: #314230;
}
